.color-ribbon {
  display: flex;
  padding: 2px;
  background: #ebedf0;
  height: 10px;
  box-sizing: border-box;
}

.color-ribbon__item {
  height: 6px;
  width: 100%;
}

.color-ribbon__item:first-child {
  border-radius: 3px 0 0 3px;
}

.color-ribbon__item:last-child:not(:first-child) {
  border-radius: 0 3px 3px 0;
}

.color-ribbon__item--animated {
  animation: color-slide-in .4s ease-out forwards;
}

.color-ribbon__item--b {
  background: #2883ce;
}

.color-ribbon__item--r {
  background: #d8572b;
}

.color-ribbon__item--y {
  background: #d7bc00;
}

.color-ribbon__item--g {
  background: #69b200;
}

@keyframes color-slide-in {
  0% { width: 0; }
  100% { width: 100%; }
}

.tile-directions, .tile-help {
  text-align: center;
  padding: 16px;
}

.tile-directions {
  font-size: 1.25em;
}

.tile-help {
  color: var(--text_secondary);
}

.tile-button {
  margin: 8px;
  display: inline-block;
  width: 100px;
  height: 100px;
  background-size: contain;
  border-radius: 4px;

  /* Reset default button styles */
  border: none;
  outline: inherit;
}

.tile-row {
  display: flex;
  justify-content: center;
}

.tile-container-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
}

.tile-container--init-transition,
.tile-container--turn-transition,
.tile-container--win-transition {
  /* https://easings.net/en#easeInOutSine */
  animation: container .8s cubic-bezier(0.445, 0.05, 0.55, 0.95) forwards;
}

.tile-container--init-transition .tile-button,
.tile-container--turn-transition .tile-button {
  animation: tile .8s cubic-bezier(0.445, 0.05, 0.55, 0.95) forwards;
}

.tile-container--init > div:last-child > .tile-button {
  opacity: 0;
}

.tile-container--init-transition > div:first-child > .tile-button {
  animation: init-visible-row-tile .8s cubic-bezier(0.445, 0.05, 0.55, 0.95) forwards;
}

.tile-container--init-transition > div:last-child > .tile-button {
  animation: init-hidden-row-tile .8s cubic-bezier(0.445, 0.05, 0.55, 0.95) forwards;
}

.tile-container--fade-out-transition {
  animation: fade-out .8s ease-in forwards;
}

.tile-container--win-transition .tile-button {
  animation: tile-win .8s cubic-bezier(0.445, 0.05, 0.55, 0.95) forwards;
}

.tile-container--hidden {
  display: none;
}

.tile-container--retry-init-transition {
  animation: fade-in .4s ease-in forwards;
  animation-delay: .4s;
  display: none;
}

.message {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.message--fade-in {
  animation: fade-in .2s ease-in forwards;
}

.message--fade-out {
  animation: fade-out .4s ease-in forwards;
}

@keyframes container {
  0% { transform: rotate(0deg); }
  50% { transform: rotate(180deg); }
  100% { transform: rotate(360deg); }
}

@keyframes tile {
  0% { transform: scale(1); border-radius: 0; }
  50% { transform: scale(0.6); border-radius: 50%; }
  100% { transform: scale(1); border-radius: 0; }
}

@keyframes tile-win {
  0% { transform: scale(1); border-radius: 0; }
  50% { transform: scale(0); border-radius: 50%; }
  100% { transform: scale(0); }
}

@keyframes init-visible-row-tile {
  0% { transform: scale(1); border-radius: 0; }
  50% { transform: scale(0); border-radius: 50%; }
  100% { transform: scale(1); border-radius: 0; }
}

@keyframes init-hidden-row-tile {
  0% { transform: scale(0); }
  50% { transform: scale(0); border-radius: 50%; opacity: 1; }
  100% { transform: scale(1); border-radius: 0; opacity: 1; }
}

@keyframes fade-out {
  0% { opacity: 1; display: block; }
  100% { opacity: 0; display: none; }
}

@keyframes fade-in {
  0% { opacity: 0; display: block; }
  100% { opacity: 1; display: block; }
}

.map {
  max-width: 260px;
}

.map-node--available {
  fill: white;
  stroke: #6991bd;
}

.map-node--available:hover,
.map-node--available:focus {
  fill: #dbe3eb;
}

.map-node--completed {
  fill: #9eb7d1;
  stroke: #587fa6;
}

.map-node--locked {
  stroke: #aeb8c2;
  fill: #ecf0f3;
}

.map-path--locked {
  stroke: #aeb8c2;
}

.map-path--completed,
.map-path--available {
  stroke: #9eb7d1;
}

.map-path {
  stroke-width: 1.2;
  fill: none;
}

.monster-main, .monster-action-tip {
  margin: 24px 0 0;
  padding: 0 24px;
  text-align: center;
}

.monster-main {
  flex-grow: 1;
}

.monster-action-qr {
  max-width: 200px;
  flex-grow: 1;
}

.monster-buttons {
  margin-bottom: 24px;
}

.monster-buttons > button:not(:first-child) {
  margin-left: 8px;
}

.monster-sprite {
  display: block;
  margin: 0 auto 24px;
  max-width: 100%;
}

.forest-letter-container {
  display: flex;
  justify-content: center;
  padding: 24px 0;
}

.forest-letter {
  width: 18vw;
  height: 18vw;
  margin: 8px;
  line-height: 18vw;

  border: 1px solid;
  border-radius: 6px;
  box-shadow: inset 0px 0px 6px 0px;
}

.forest-letter:hover, .forest-letter:focus {
  box-shadow: inset 0px 0px 16px 0px;
}

.forest-letter__text {
  font-size: 2em;
  color: #000;
}

.forest-letter--b {
  background: #d4ebff;
  color: #9eb7d1;
}

.forest-letter--r {
  background: #f2c2b1;
  color: #d19a85;
}

.forest-letter--y {
  background: #f4eeab;
  color: #cbc47e;
}

.forest-letter--g {
  background: #c5e695;
  color: #9ab474;
}

.forest-popout {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 192px;
  margin: auto 24px;
  padding: 24px;

  background-color: #fff;
  border-radius: 12px;
}

.forest-popout--android {
  box-shadow: 0 20px 35px 0 #0000004d, 0 0 35px 0 #00000038;
}

.forest-popout__letter {
  width: 80px;
  height: 80px;
  margin: 8px;

  font-size: 40px;
  background: #fff;
  border: 1px solid #9a9a9a;
  border-radius: 12px;
}

.forest-popout__letter:hover, .forest-popout__letter:focus {
  background: #eceaea;
}

